/* Homepage styles */
.homepage {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f2f2f2;
}

.homepage-content {
  text-align: center;
  padding: 20px;
}

.homepage-title {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
}

.homepage-name {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #007bff;
}

.homepage-description {
  font-size: 20px;
  color: #555;
}

.check-demo {
  font-size: 18px;
  color: #555;
  margin-bottom: 5px;
}

.portfolio-btn {
  display: inline-block;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 30px;
  padding: 16px 40px;
  font-size: 20px;
  text-decoration: none;
  margin-top: 10px;
  transition: background-color 0.2s ease-in-out;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.portfolio-btn:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.about-summary {
  font-size: 18px;
  color: #333;
  margin-top: 20px;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
